/** @format */

import axios from 'axios';
import React, { useEffect, useState } from 'react';
import {
  useNavigate,
  useParams,
  useSearchParams,
  useLocation,
} from 'react-router-dom';
import {
  Heading,
  Text,
  Image,
  Container,
  Link,
  HStack,
  Box,
  Button,
  AlertTitle,
  AlertDescription,
  CloseButton,
} from '@chakra-ui/react';

import { useMoralis } from 'react-moralis';
import * as GLOBAL from '../model/global';
import * as HELPER from '../model/helper';
import * as STATIC from '../model/static';
import * as API from '../io/api';

export default function Message() {
  const [connectionUser, setConnectionUser] = useState('');
  const [connectionDid, setConnectionDid] = useState('');
  const [isConnected, setIsConnected] = useState(false);
  const [searchParams, setSearchParams] = useSearchParams();

  const {
    isAuthenticated,
    isAuthenticating,
    authError,
    authenticate,
    login,
    logout,
    user,
  } = useMoralis();

  const navigate = useNavigate();

  const closeNow = () => {
    window.close();
  };

  const visit = () => {
    navigate('/show/' + connectionDid);
  };

  useEffect(() => {
    if (connectionDid == '') {
      GLOBAL.setAppstate('pagestate', 'MESSAGE');
      const did = searchParams.get('did'); //query string
      if (did == null || did == '') {
        return;
      }
      setConnectionDid(did);
      GLOBAL.setAppstate('messageDid', did);
      if (user) {
        setIsConnected(true);
        if (GLOBAL.getAppState().token == '') {
        }
      }
      GLOBAL.loadYouByPubKey(HELPER.PubkeyFromDid(did)).then((person) => {
        GLOBAL.setAppstate('didfound', true);
        if (person == null || JSON.stringify(person) == '{}') {
          GLOBAL.setAppstate('didfound', false);
          return;
        }
        setConnectionUser(person.username);
      });
    }
  });

  return (
    <Container>
      <Heading>Message</Heading>
      <br />
      <Text>Thank you, {connectionUser} is a new connection.</Text>
      <br />
      <br />
      <Button onClick={visit}>View {connectionUser}</Button>

      {!isConnected && (
        <Text fontSize="xs" color="blue.500">
          To access your new contact please login first.
        </Text>
      )}
    </Container>
  );
}
